
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { validateNotNull } from 'util/rules'
export default {
data () {
return {
show1: false,
show2: false,
newDate: '',
hccList: [],
hcc: '',
foodTeaching: {
appointmentDate: '',
arriveStore: 0,
clientInfoId: 0,
createBy: '',
createTime: '',
dishName: '',
id: null,
receptionist: '',
remark: '',
type: '2',
updateBy: '',
updateTime: ''
}
}
},
methods: {
saveService () {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候~')
return
}
let data = {
clientInfoId: this.foodTeaching.clientInfoId,
type: 2,
foodTeaching: this.foodTeaching
}
if (validateNotNull(this, this.foodTeaching.appointmentDate, '预约日期') && validateNotNull(this, this.foodTeaching.dishName, '教做菜品') && validateNotNull(this, this.foodTeaching.receptionist, '接待人员')) {
this.inited = true
this.$tupHttp(this, 'client-biz/client-biz/storeClient/saveService', 'POST', data).then(res => {
this.$vux.toast.text('保存成功！')
setTimeout(() => {
this.$router.push({
path: '/customerSRV',
query: {
id: this.$route.query.id,
type: 2
}
})
}, 500)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}
},
getAppointmentDate () {
this.show1 = false
this.foodTeaching.appointmentDate = this.newDate
},
getReceptionist () {
this.show2 = false
this.foodTeaching.receptionist = this.hcc
},
_getHccList () {
this.$tupHttp(this, 'client-biz/client-biz/storeClient/hccList', 'GET').then(res => {
this.hccList = res.data.models
})
},
getTime () {
var _this = this
let yy = new Date().getFullYear()
let mm = new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1
let dd = new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate()
_this.newDate = yy + '-' + mm + '-' + dd
}
},
created () {
this.foodTeaching.clientInfoId = parseInt(this.$route.query.id)
this.getTime()
this._getHccList()
},
components: {
XHeader,
PopupHeader,
Popup,
Group,
Radio,
Datetime,
XTextarea
}
}



